
html{font-size:20px
}
body{font-family:"Helvetica Neue", Helvetica, STHeiTi, sans-serif
}
a,img{-webkit-touch-callout:none
}
html,body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;min-height:100%;height:100%
}
button,input,optgroup,select,textarea{-webkit-appearance:none
}
a,button,input,optgroup,select,textarea{-webkit-tap-highlight-color:transparent
}
input::-webkit-input-placeholder{color:#ccc
}
input:focus::-webkit-input-placeholder{color:#f00
}
body{-webkit-text-size-adjust:100% !important
}
input::-webkit-input-speech-button{display:none
}
.clear{display:block !important;clear:both !important;float:none !important;margin:0 !important;padding:0 !important;height:0;line-height:0;font-size:0;overflow:hidden
}
.clearfix{zoom:1
}
.clearfix:after{content:"";display:block;clear:both;height:0
}
.view{transition:all 0.5s cubic-bezier(0.55, 0, 0.1, 1)
}
.fade-enter-active,.fade-leave-active{transition:opacity .3s ease
}
.fade-enter,.fade-leave-active{opacity:0
}
.slide-left-enter,.slide-right-leave-active{opacity:0;transform:translate(30px, 0)
}
.slide-left-leave-active,.slide-right-enter{opacity:0;transform:translate(-30px, 0)
}
.slide-down-enter,.slide-down-leave-active{opacity:0;transform:translate(0, -30px)
}
.slide-down-enter-active,.slide-down-leave{transition:all .5s ease
}
.flex{display:-ms-flexbox;display:flex
}
.flex-all__center{-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.flex-column__between{-ms-flex-pack:justify;justify-content:space-between
}
.flex-column__around{-ms-flex-pack:distribute;justify-content:space-around
}
.flex-row{-ms-flex-direction:row;flex-direction:row
}
.flex-column{-ms-flex-direction:column;flex-direction:column
}
.app-nav{position:absolute;top:0;bottom:0;left:0;right:0;width:12.5rem;background-color:transparent;color:#666
}
.app-nav a{color:#666
}
.app-nav ul,.app-nav li{list-style:none;padding:0;margin:0
}
.view{position:absolute;top:0;bottom:0;right:0;left:12.5rem
}



